::selection {
  background: rgba($color-dark, 0.2);
}

::-moz-selection {
  background: rgba($color-dark, 0.2);
}

:root {
  font-size: $root-font-size;
}

body {
  font-family: $font-primary;
  font-weight: 400;
  color: $color-dark;
  line-height: 1.618;
  background-color: $color-light;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-secondary;
  font-weight: 700;
}

a {
  color: $color-dark;
}

section {
  margin-bottom: 10vmin;
}
